function weatherType(type) {
  let weathericon = '';
  switch(type) {
    case "": weathericon = "cloudly";						break;
    case "": weathericon = "overcast-sky";						break;
    case "": weathericon = "sunshine";					break;
    case "": weathericon = "sprinkle";					break;
    case "": weathericon = "sprinkle";					break;
    case "": weathericon = "moderateRain";				break;
    case "": weathericon = "heavyRain";			break;
    case "": weathericon = "rainstorm";			break;
    case "": weathericon = "littleSnow";				break;
    case "": weathericon = "moderateSnow";				break;
    case "": weathericon = "heavySnow";				break;
    case "": weathericon = "sleet";				break;
    case "": weathericon = "thundershower";				break;
    default : weathericon = "icon-Cloud";							break;
  }
  return weathericon;
}
function weatherTypeN(type) {
  let weathericon = '';
  switch(type) {
    case "": weathericon = "nightCloudly";						break;
    case "": weathericon = "overcast-sky";						break;
    case "": weathericon = "sunnyNight";					break;
    case "": weathericon = "sprinkle";					break;
    case "": weathericon = "sprinkle";					break;
    case "": weathericon = "moderateRain";				break;
    case "": weathericon = "heavyRain";			break;
    case "": weathericon = "rainstorm";			break;
    case "": weathericon = "littleSnow";				break;
    case "": weathericon = "moderateSnow";				break;
    case "": weathericon = "heavySnow";				break;
    case "": weathericon = "sleet";				break;
    case "": weathericon = "thundershower";				break;
    default : weathericon = "icon-Cloud";							break;
  }
  return weathericon;
}

export{
  weatherType,
  weatherTypeN
}



